import React from 'react';
import { shape, object, arrayOf, bool, func } from 'prop-types';
import Link from 'gatsby-link';
import cx from 'classnames';

import { Icon } from '../../ui';
import Navigation from '../../components/Navigation/Navigation';

import './Header.scss';

const getNavItems = (items) =>
	items.filter(i => {
		const scope = i.node.frontmatter.scope;
		return scope && scope.indexOf('HEADER') > -1
	});

const Header = ({ data, isTop, scrollToTop, withNav }) => {
	const shadowClass = cx({
		'header__shadow': true,
		'header__shadow--visible': !isTop
	});

	return (
		<div>
			<div className="header__container">
				<div className="header__content">
					<Link to="/"
						className="logo__container">
						<Icon image="logo" />
					</Link>

					{
						withNav &&
						(
							<Navigation 
								pages={getNavItems(data.allMarkdownRemark.edges)}
								isTop={isTop}
							/>
						)
					}
				</div>

			</div>
			
			<div className={shadowClass} />
		</div>
	);
}

Header.propTypes = {
	data: shape({
		allMarkDownRemark: shape({
			edges: arrayOf(object)
		})
	}),
	isTop: bool,
	withNav: bool,
	scrollToTop: func
}

export default Header


